@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400&display=swap);
* {
  box-sizing: border-box;
}

body {
  font-family: "Raleway", Arial, Helvetica, sans-serif;
  width: 95%;
  margin: 0 auto;
}

header {
  margin: 16px auto 0 auto;
  background: #fff;
  padding: 50px 0;
  box-shadow: 0px 5px 0px #dedede;
  text-align: center;
}

nav ul {
  list-style: none;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

nav ul li {
  display: inline-block;
}

nav ul li a {
  display: block;
  padding: 15px;
  text-decoration: none;
  color: #aaa;
  font-weight: 800;
  text-transform: uppercase;
  margin: 0 10px;
}

nav ul li a,
nav ul li a:after,
nav ul li a:before {
  transition: all 0.5s;
}

nav ul li a:hover,
nav ul li a.active {
  color: #555;
}

strong {
  display: block;
  margin: 1rem;
}

.calculator {
  background-color: white;
  color: black;
  padding: 1rem;
  margin: 1rem 0;
}

.calculator .button {
  margin: 15px;
}

.button:disabled {
  background-color: #a5817d;
}

input {
  display: block;
  margin: 1rem;
  padding: 1rem;
  width: 33%;
  color: black;
  font-family: "Raleway", Arial, Helvetica, sans-serif;
}

@media screen and (max-width: 500px) {
  input {
    width: 90%;
  }
}

body {
  background-color: salmon;
}

.card-container {
  display: flex;
  flex-wrap: wrap;

  margin: 0 auto;
}

.card {
  background-color: white;
  justify-content: space-between;
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  padding: 20px;
  margin: 2rem;
  box-shadow: -20px -20px 0 0 #ff9d92;
  border-radius: 10px;
  animation-name: shadow-show;
  animation-duration: 1.5s;
  width: 16%;
  min-height: -webkit-min-content;
  min-height: -moz-min-content;
  min-height: min-content;
  transition-timing-function: cubic-bezier(0.795, 0, 0.165, 1); /* custom */
}

.warning {
  font-size: 13px;
}

.card p {
  flex-grow: 1;
}

.fas {
  margin-right: 10px;
}

@media screen and (max-width: 1440px) {
  .card {
    width: 20%;
  }
}

@media screen and (max-width: 1028px) {
  .card {
    width: 26%;
  }
}

@media screen and (max-width: 768px) {
  .card {
    width: 41%;
  }
}

@media screen and (max-width: 500px) {
  .card {
    width: 100%;
  }
}

.card h1,
.card h2 {
  margin: 0;
  padding: 0 0 15px 0;
  font-size: 1.3rem;
  color: #282828;
}

.card hr {
  display: block;
  border: none;
  height: 3px;
  background-color: salmon;
  margin: 0;

  animation-name: line-show;
  animation-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.795, 0, 0.165, 1); /* custom */
}

.card p {
  margin: 15px 0 0 0;
  font-weight: 100;
  letter-spacing: -0.25px;
  line-height: 1.25;
  font-size: 16px;
  color: #282828;
  animation-duration: 1.5s;
}

.button {
  border: none;
  background-color: salmon;
  margin: 15px auto;
  padding: 10px 30px;
  color: white;
  text-transform: uppercase;
  cursor: pointer;
}

.card strong {
  display: inline;
  display: initial;
  margin: 0;
  margin: initial;
}

.panel {
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
  animation: fadein 2s;
}

.panel h4 {
  text-transform: uppercase;
  margin: 0;
}

.active,
.button:hover:not([disabled]) {
  background-color: #ccc;
  color: black;
}

.panel {
  width: auto;
  width: initial;
  padding: 18px;
  background-color: #ccc;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  color: salmon;
}

@keyframes line-show {
  from {
    margin: 0 100px;
  }
  to {
    margin: 0;
  }
}

@keyframes p-show {
  from {
    color: white;
  }
  to {
    color: #282828;
  }
}

@keyframes shadow-show {
  from {
    box-shadow: 0 0 0 0 #e0e0e0;
  }
  to {
    box-shadow: -20px -20px 0 0 #ff9d92;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.fourohfour {
  display: flex;
}

.fourohfour img {
  width: 100%;
}

.fourohfour div:first-of-type {
  width: 60%;
}

.fourohfour div:last-of-type {
  width: 40%;
}

